<template>
  <div class="mt-30 container text-center">
    <div v-if="errorMessage" class="alert alert-warning alert-dismissible fade show" role="alert">
      <strong>{{ errorMessage }}</strong>
    </div>
    <form @submit.prevent="signup">
      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Логин:</label>
        <div class="col-sm-10">
        <input
          v-model="user.email"
          type="text" id="email" class="form-control" required>
        </div>
      </div>
      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Пароль:</label>
        <div class="col-sm-10">
        <input
          v-model="user.password"
          type="password" id="password" class="form-control" required>
        </div>
      </div>

      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Еще раз:</label>
        <div class="col-sm-10">
        <input
          v-model="user.confirmPassword"
          type="password" id="confirmPassword" class="form-control" required>
        </div>
      </div>

      <img v-if="signingUp" class="spinner" src="../assets/spinner.svg"/>
      <button v-else type="submit" class="btn btn-primary">Зарегистрироваться</button>
    </form>
  </div>
</template>

<script>
import Joi from 'joi';

const SIGNUP_URL = 'http://localhost:5000/auth/signup';

const schema = Joi.object().keys({
  email: Joi.string().required(),
  password: Joi.string().trim().min(6).required(),
  confirmPassword: Joi.string().trim().min(6).required(),
});

export default {
  data: () => ({
    signingUp: false,
    errorMessage: '',
    user: {
      email: '',
      password: '',
      confirmPassword: '',
    },
  }),
  watch: {
    user: {
      handler() {
        this.errorMessage = '';
      },
      deep: true,
    },
  },
  methods: {
    validUser() {
      this.errorMessage = '';

      if (this.user.password !== this.user.confirmPassword) {
        this.errorMessage = 'Пароли не совпадают';
        return false;
      }

      const result = Joi.validate(this.user, schema);

      if (result.error === null) {
        return true;
      }

      if (result.error.message.includes('email')) {
        this.errorMessage = 'Неверный имейл';
      } else {
        this.errorMessage = 'Пароль должен содержать 6+ символов';
      }
      return true;
    },
    signup() {
      if (this.validUser()) {
        const body = {
          email: this.user.email,
          password: this.user.password,
        };
        this.signingUp = true;
        fetch(SIGNUP_URL, {
          method: 'POST',
          body: JSON.stringify(body),
          headers: {
            'content-type': 'application/json',
          },
        })
          .then((res) => {
            if (res.ok) {
              return res.json();
            }
            return res.json()
              .then((err) => {
                throw new Error(err.message);
              });
          })
          .then((res) => {
            localStorage.token = res.token;
            this.signingUp = false;
            this.$store.commit('setUser', res);
            this.$router.push('/');
          })
          .catch((err) => {
            this.signingUp = false;
            this.errorMessage = err.message;
          });
      }
    },
  },
};
</script>

<style scoped>
  .mb-30 {
    margin-bottom: 30px;
  }

  .mt-30 {
    margin-top: 30px;
  }

  .ps-250 {
    padding-left: 250px;
    padding-right: 250px;
  }

  .spinner {
    height: 100px;
    width: 100px;
  }
</style>
